@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}

@mixin hover {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }
}

@mixin focus {
  &.focus-visible:focus {
    @content;
  }
}

@mixin active {
  &.focus-visible:active {
    @content;
  }
}

@mixin hover-focus {
  @media (hover: hover) {
    &:hover:not(.focus-visible) {
      @content;
    }
  }

  &.focus-visible:focus {
    @content;
  }
}

// Desktop first

@mixin vp-1920 {
  @media (max-width: $vp-1920) {
    @content;
  }
}

@mixin vp-1440 {
  @media (max-width: $vp-1440) {
    @content;
  }
}

@mixin vp-1280 {
  @media (max-width: $vp-1280) {
    @content;
  }
}

@mixin vp-1024 {
  @media (max-width: $vp-1024) {
    @content;
  }
}

@mixin vp-768 {
  @media (max-width: $vp-768) {
    @content;
  }
}

@mixin vp-540 {
  @media (max-width: $vp-540) {
    @content;
  }
}

@mixin vp-360 {
  @media (max-width: $vp-360) {
    @content;
  }
}

